.body {
    padding: 30px 50px;
}

.smallText,
.fileName {
    font-weight: 200;
    font-size: 14px;
}

.fileName {
    margin-top: 39px;
    margin-left: 10px;
    white-space: nowrap;
    overflow-x: hidden;
    text-overflow: ellipsis;
}

.smallerText {
    font-weight: 400;
    font-size: 12px;
    color: #6D6D73;
    margin-top: 3px;
}

.buttons {
    text-align: right;
}

.sampleTemplateText {
    color: #008662;
    cursor: pointer;
}

.errorMessage {
    margin-top: -15px;
    color: red;
    font-size: 14px;
}