.buttons{
margin-top: 30px;
text-align: right;
}

.dateRangeSection {
width: 60%;
}

.buttonDiv{
padding-right: 20px;
}

.p{
margin-right: 5px;
}

.formInput {
margin-top: 3.8px;
height: 32px;
width: 140px;
padding: 8px 13px;
color: #343a40;
font-size: 12px;
border-radius: 4px;
border: 1px solid #d3d2d2;
}

.overlappingVerbiageText {
margin-left: 13px;
color: #FF0000;
font-size: 12px;
}

.redBorder {
    border-color: #FF0000;
}

.errorText {
    color: #FF7878;
    font-size: 12px;
    margin-bottom: 7px;
}

.dialogBody{
padding: 30px 50px;
border-radius: 5px;
height: 100%;
}

.titleDialog{
width: 666px;
height: 25px;
font-family: 'Poppins';
font-style: normal;
font-weight: 300;
font-size: 20px;
letter-spacing: -0.05em;
color: #000000;
}

.description{
margin-top: 38px;
width: 666px;
height: 21px;
font-family: 'Poppins';
font-style: normal;
font-weight: 400;
font-size: 14px;
color: #343A40;
}

.titleScope{
height: 350px;
overflow-y: auto;
}

.titleListColumn1{
width: 36%;
padding-right: 0px;
margin-right: 0px;
margin-top: 10px;
}

.titleListColumn2{
width: 64%;
}

.title{
width: 200px;
height: 21px;
font-family: 'Poppins';
font-style: normal;
font-weight: 400;
font-size: 14px;
line-height: 21px;
color: #343A40;
}

.effectivityDatesDiv{
width: 89%;
font-size: 14px;
color: #343A40; 
}

.iconDiv{
margin-left: 5px;
padding-top: 10px;
padding: 6px;
}

.trashIcon:hover{
cursor: pointer;
}

.addIcon:hover{
cursor: pointer;
}

.addIcon{
height: 20px;
transform: rotate(-45deg);
width: auto;
margin-top: 6px;
}

.trashIcon{
height: 20px;
width: auto;
margin-top: 6px;
}

.checkButton{
margin-top: 10px;
margin-bottom: 10px;
}