.modal {
    width: 979px;
    height: 559px;
    max-width: 979px;
    max-height: 559px;
    margin-left: -14em;
}

.dialogContent {
    padding: 30px;
}

.description {
    text-align: center;
    font-size: 14px;
    color: #6D6D73;
}

.description b {
    color: #000000;
}

.table {
    border: 1px solid #E2E8F0;
    margin-top: 20px;
}

.tableHeader,
.tableBodyRow {
    display: flex;
    border: 1px solid #E2E8F0;
    border-left: none;
}

.tableHeader {
    border-top: none;
    border-right: none;
}

.tableHeader div {
    padding: 11px 30px 11px 21px;
    font-weight: 600;
    color: #656565;
    font-size: 12px;
    text-transform: uppercase;
}

.tableHeader div:nth-child(1) {
    width: 162px;
}

.tableHeader div:nth-child(2) {
    width: 120px;
}

.tableHeader div:nth-child(3),
.tableHeader div:nth-child(4) {
    width: 125px;
}

.tableHeader div:nth-child(5) {
    width: 37px;
}

.tableHeader div:nth-child(6) {
    width: 335px;
}

.tableBodyRow {
    border-top: none;
    border-right: none;
    color: #6D6D73;
    font-size: 12px;
}

.tableBodyRow div {
    padding: 11px 30px 11px 21px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow-x: hidden;
}

.tableBodyRow div:nth-child(1) {
    width: 162px;
}

.tableBodyRow div:nth-child(2) {
    width: 120px;
}

.tableBodyRow div:nth-child(3),
.tableBodyRow div:nth-child(4) {
    width: 125px;
}

.tableBodyRow div:nth-child(5) {
    width: 37px;
}

.tableBodyRow div:nth-child(6) {
    width: 335px;
}

.tableBody {
    height: 323px;
    overflow-y: auto;
}

.tableBody::-webkit-scrollbar-thumb {
    background-color: #D2D2D2;
    box-shadow: inset 0 0 0 2px #ffffff;
    border-left: 1px solid #E2E8F0;
}
  
.tableBody::-webkit-scrollbar-track {
    border-left: 1px solid #E2E8F0;
}
  
.tableBody::-webkit-scrollbar {
    background-color: #ffffff;
    width: 14px;
    padding: 5px;
}

.radioButtons {
    margin-top: 20px;
    display: flex;
}

.closeButton {
    margin-top: 10px;
    width: 100%;
    text-align: right;
}

.formCheckInput {
    margin-right: 10px;
    font-size: 14px;
}

.formCheck {
    display: flex;
    margin-right: 20px;
}
  
.formCheckInput:checked {
    background-color: #008662;
    border-color: #008662;
    box-shadow: none;
}

.formCheckInput:hover {
    cursor: pointer;
}

.formCheckInputLabel {
    font-size: 12px;
    color: #212529;
    margin-top: 2px;
}