.checkboxDiv {
  display: flex;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.box {
  border-radius: 4px;
}

.circle {
  border-radius: 50%;
}

.box,
.circle {
  width: 13px;
  height: 13px;
  border: 1px solid #DEE2E6;
  border-radius: 2px;
  position: relative;
  cursor: pointer;
  padding-left: 1px;
  margin-right: 4px;
  margin-top: 1px;
}

.notChecked {
  background-color: #ffffff;
}

.checked {
  background-color: #008662;
}

.checkIcon {
  padding-bottom: 4px;
  content: url('../../../shared/assets/icons/check.svg');
}

.label {
  width: 189px;
  height: 18px;  
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  color: #6D6D73;
}

.disabled {
  pointer-events: none;
}

.disabledNotChecked {
  border: 1px solid #b1b1b1;
}

.disabledChecked {
  background-color: #b1b1b1;
  border: 1px solid #b1b1b1;
}
