.dualTable {
  display: flex;
}

.tableOne {
  width: 60%;
}

.tableTwo {
  width: 33%;
}

.actionButtons {
  min-width: 102px;
  max-width: 102px;
  margin-left: 6px;
  margin-right: 6px;
  justify-content: center;
  display: flex;
  flex-direction: column;
  padding-top: 42px;
}

.actionButtons button {
  display: flex;
  height: 38px;
  padding: 1px 5px 0px 5px;
  background-color: #008662;
  font-size: 12px;
  font-weight: 100;
  border-radius: 4px;
  border: 1px solid #008662;
  color: #ffffff;
}

.actionButtons button + button {
  margin-top: 5px;
}

.actionButtons button:active,
.actionButtons button:hover {
  background-color: #116c53;
  border: 1px solid #116c53;
}

.actionButtons button:focus {
  border: 1px solid #116c53;
  outline: none;
}

.actionButtons button:disabled {
  background-color: #b1b1b1;
  border: 1px solid #b1b1b1;
  cursor: not-allowed;
}

.tableLabel {
  color: #6d6d73;
  font-size: 14px;
  font-weight: 400;
  margin-bottom: 3px;
}

.searchWrapper {
  display: flex;
  width: 100%;
  margin-bottom: 5px;
}

.search {
  height: 28px;
  width: 100%;
  border: 1px solid #b1b1b1;
  border-right: none;
  border-radius: 4px 0px 0px 4px;
  font-size: 12px;
  text-indent: 5px;
}

.search:focus {
  outline: none;
}

.timesIcon {
  content: url('../../assets/icons/times.svg');
  width: 12.7px;
  height: auto;
  margin-top: 6px;
  margin-left: 10px;
  cursor: pointer;
}

.searchIcon {
  content: url('../../assets/icons/magnifying-glass.svg');
  margin-top: 6px;
  margin-left: 5px;
  cursor: pointer;
}

.timesIcon:hover,
.searchIcon:hover {
  filter: brightness(0) saturate(100%) invert(30%) sepia(86%) saturate(1538%)
    hue-rotate(142deg) brightness(87%) contrast(101%);
}

.timesIconWrapper {
  width: 30px;
  background-color: #ffffff;
  border: 1px solid #b1b1b1;
  border-left: none;
  border-right: none;
}

.searchIconWrapper {
  width: 30px;
  background-color: #ffffff;
  border: 1px solid #b1b1b1;
  border-left: none;
  border-radius: 0px 4px 4px 0px;
  height: 28px;
}

.noDataComponent {
  width: 100%;
  background-color: #e8e8e8;
  border-radius: 4px;
  position: relative;
}

.textWrapper {
  position: absolute;
  width: 90%;
  height: auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.verbiage {
  color: #008662;
  font-weight: 300;
  font-size: 24px;
  text-align: center;
}

.dragButton {
  background-color: #008662;
  border: none;
  border-radius: 4px;
  margin-top: 5px;
  width: 30px;
  height: 30px;
  margin-left: 5px;
  display: block;
}

.dragButton:first-child {
  margin-top: 0px;
}

.dragButton:disabled {
  background-color: #b1b1b1;
  cursor: not-allowed;
}