.cancelButton {
  height: 36px;
  font-size: 12px;
  line-height: 12px;
  font-weight: 100;
  padding: 11px 24px;
  background-color: #f6f6f6;
  color: #5c636a;
  border: none;
  border-radius: 4px;
  width: 100%;
  margin-top: 20px;
}

.saveModalButton {
  height: 36px;
  font-size: 12px;
  line-height: 12px;
  font-weight: 100;
  padding: 11px 24px;
  background-color: #008662;
  color: #ffffff;
  border: none;
  border-radius: 4px;
  width: 100%;
  margin-top: 20px;
}

.saveModalButton:disabled {
  background-color: #b1b1b1;
  border: 1px solid #b1b1b1;
  cursor: not-allowed;
}

.selectChannel {
  margin-top: 16px;
  display: flex;
}

.selectChannelLabel {
  margin-top: 4px;
  margin-right: 9px;
}

.uploadFileIcon,
.downloadFileIcon {
  margin-left: 15px;
  cursor: pointer;
}

.disabledDownloadFileIcon,
.disabledUploadFileIcon {
  cursor: not-allowed;
  filter: invert(78%) sepia(0%) saturate(0%) hue-rotate(59deg) brightness(93%) contrast(86%);
}

.isExportingDownloadFileIcon {
  cursor: wait;
  filter: invert(78%) sepia(0%) saturate(0%) hue-rotate(59deg) brightness(93%) contrast(86%);
}